import { ref } from "vue";
import { defineStore } from "pinia";
import { supabase } from "@/lib/supabaseClient"; // Supabase 
import type { Database } from "@/types/database"; // Supabase 

//  Category 
type Category = Database["public"]["Tables"]["categories"]["Row"];

export const useCategoriesStore = defineStore("categories", () => {
  // --- State () ---
  const categories = ref<Category[]>([]);
  const loading = ref(false);
  const error = ref<Error | null>(null);
  // 
  const initialized = ref(false);

  // --- Actions () ---

  /**
   *  Supabase 
   * 
   */
  async function fetchCategories() {
    // 
    if (loading.value) return;

    loading.value = true;
    error.value = null;
    try {
      // 
      const supabaseUrl = import.meta.env.VITE_SUPABASE_URL;
      const supabaseAnonKey = import.meta.env.VITE_SUPABASE_ANON_KEY;
      
      if (!supabaseUrl || !supabaseAnonKey || supabaseUrl.includes('your-project-ref') || supabaseAnonKey.includes('your-anon-key')) {
        // 
        console.warn('Supabase ');
        categories.value = [
          {
            id: '1',
            name: '',
            description: '',
            icon: '',
            color: '#3b82f6',
            parent_id: null,
            sort_order: 1,
            is_active: true,
            created_at: new Date().toISOString(),
            updated_at: new Date().toISOString()
          },
          {
            id: '2',
            name: '',
            description: 'UI/UX',
            icon: '',
            color: '#ef4444',
            parent_id: null,
            sort_order: 2,
            is_active: true,
            created_at: new Date().toISOString(),
            updated_at: new Date().toISOString()
          },
          {
            id: '3',
            name: 'AI',
            description: '',
            icon: '',
            color: '#10b981',
            parent_id: null,
            sort_order: 3,
            is_active: true,
            created_at: new Date().toISOString(),
            updated_at: new Date().toISOString()
          },
          {
            id: '4',
            name: '',
            description: '',
            icon: '',
            color: '#f59e0b',
            parent_id: null,
            sort_order: 4,
            is_active: true,
            created_at: new Date().toISOString(),
            updated_at: new Date().toISOString()
          },
          {
            id: '5',
            name: '',
            description: '',
            icon: '',
            color: '#8b5cf6',
            parent_id: null,
            sort_order: 5,
            is_active: true,
            created_at: new Date().toISOString(),
            updated_at: new Date().toISOString()
          }
        ];
        initialized.value = true;
        return;
      }

      const { data, error: queryError } = await supabase
        .from("categories")
        .select("*")
        .eq("is_active", true) // 
        .order("sort_order", { ascending: true }); //  sort_order 

      if (queryError) {
        throw queryError;
      }

      categories.value = data || [];
      initialized.value = true; // 
    } catch (e: any) {
      console.error(":", e);
      error.value = e;
      
      // Supabase
      if (categories.value.length === 0) {
        console.warn('Supabase');
        categories.value = [
          {
            id: '1',
            name: '',
            description: '',
            icon: '',
            color: '#3b82f6',
            parent_id: null,
            sort_order: 1,
            is_active: true,
            created_at: new Date().toISOString(),
            updated_at: new Date().toISOString()
          },
          {
            id: '2',
            name: '',
            description: 'UI/UX',
            icon: '',
            color: '#ef4444',
            parent_id: null,
            sort_order: 2,
            is_active: true,
            created_at: new Date().toISOString(),
            updated_at: new Date().toISOString()
          }
        ];
        initialized.value = true;
        error.value = null; // 
      }
    } finally {
      loading.value = false;
    }
  }

  /**
   *  Store
   * 
   *  `ErrorDisplay.vue` 
   */
  async function initialize() {
    if (!initialized.value) {
      await fetchCategories();
    }
  }

  /**
   * 
   * 
   */
  function clearError() {
    error.value = null;
  }

  /**
   * fetchCategories 
   */
  async function loadCategories() {
    await fetchCategories();
  }

  // --- Return () ---
  // Getter 
  //  "is not a function" 
  return {
    // State
    categories,
    loading,
    error,
    initialized,

    // Actions
    fetchCategories, // 
    loadCategories, // 
    initialize, // 
    clearError,
  };
});
