import { createClient } from "@supabase/supabase-js";
import type { Database } from "../types/database";

// 
const supabaseUrl = import.meta.env.VITE_SUPABASE_URL;
const supabaseAnonKey = import.meta.env.VITE_SUPABASE_ANON_KEY;

// 
if (!supabaseUrl || !supabaseAnonKey || 
    supabaseUrl.includes('your-project-ref') || 
    supabaseAnonKey.includes('your-anon-key')) {
  console.warn(`
      Supabase !

    :  .env.local :
      VITE_SUPABASE_URL  VITE_SUPABASE_ANON_KEY

    :  Netlify :
      VITE_SUPABASE_URL  VITE_SUPABASE_ANON_KEY

    :
      VITE_SUPABASE_URL: ${supabaseUrl || ""}
      VITE_SUPABASE_ANON_KEY: ${supabaseAnonKey ? "" : ""}
      
    
  `);
}

//  Supabase 
export const supabase = createClient<Database>(supabaseUrl, supabaseAnonKey, {
  auth: {
    autoRefreshToken: true,
    persistSession: true,
    detectSessionInUrl: true,
  },
});

// 
export const TABLES = {
  TOOLS: "tools",
  CATEGORIES: "categories",
  PRODUCTS: "products",
  PRODUCT_CATEGORIES: "product_categories",
  USERS: "users",
  USER_PROFILES: "user_profiles",
  FAVORITES: "favorites",
  ORDERS: "orders",
  ORDER_ITEMS: "order_items",
  PAYMENTS: "payments",
  REVIEWS: "reviews",
  TAGS: "tags",
  TOOL_TAGS: "tool_tags",
  ANALYTICS: "analytics",
} as const;

// 
export const STORAGE_BUCKETS = {
  AVATARS: "avatars",
  PRODUCT_IMAGES: "product-images",
  TOOL_ICONS: "tool-icons",
  UPLOADS: "uploads",
} as const;

// 
export const REALTIME_CHANNELS = {
  TOOLS: "tools-channel",
  PRODUCTS: "products-channel",
  ORDERS: "orders-channel",
  ANALYTICS: "analytics-channel",
} as const;

// 
export const handleSupabaseError = (error: any) => {
  console.error("Supabase Error:", error);

  if (error?.message) {
    return error.message;
  }

  if (error?.error_description) {
    return error.error_description;
  }

  return "";
};

// 
export const isAuthenticated = async () => {
  const {
    data: { session },
  } = await supabase.auth.getSession();
  return !!session;
};

// 
export const getCurrentUser = async () => {
  const {
    data: { user },
  } = await supabase.auth.getUser();
  return user;
};

// 
export const getUserRole = async (userId: string) => {
  const { data, error } = await supabase
    .from(TABLES.USER_PROFILES)
    .select("role")
    .eq("id", userId)
    .single();

  if (error) {
    console.error("Error fetching user role:", error);
    return "user";
  }

  return data?.role || "user";
};

// 
export const isAdmin = async (userId?: string) => {
  if (!userId) {
    const user = await getCurrentUser();
    if (!user) return false;
    userId = user.id;
  }

  const role = await getUserRole(userId);
  return role === "admin" || role === "super_admin";
};

// 
export const uploadFile = async (
  bucket: string,
  path: string,
  file: File,
  options?: { upsert?: boolean },
) => {
  const { data, error } = await supabase.storage
    .from(bucket)
    .upload(path, file, options);

  if (error) {
    throw new Error(handleSupabaseError(error));
  }

  return data;
};

// URL
export const getPublicUrl = (bucket: string, path: string) => {
  const { data } = supabase.storage.from(bucket).getPublicUrl(path);

  return data.publicUrl;
};

// 
export const deleteFile = async (bucket: string, path: string) => {
  const { error } = await supabase.storage.from(bucket).remove([path]);

  if (error) {
    throw new Error(handleSupabaseError(error));
  }
};

// 
export const createRecord = async (table: string, data: Record<string, any>) => {
  const { data: result, error } = await supabase
    .from(table)
    .insert(data)
    .select()
    .single();

  if (error) {
    throw new Error(handleSupabaseError(error));
  }

  return result;
};

export const updateRecord = async (
  table: string,
  id: string,
  data: Record<string, any>,
) => {
  const { data: result, error } = await supabase
    .from(table)
    .update(data)
    .eq("id", id)
    .select()
    .single();

  if (error) {
    throw new Error(handleSupabaseError(error));
  }

  return result;
};

export const deleteRecord = async (table: string, id: string) => {
  const { error } = await supabase.from(table).delete().eq("id", id);

  if (error) {
    throw new Error(handleSupabaseError(error));
  }
};

export const getRecord = async (table: string, id: string) => {
  const { data, error } = await supabase
    .from(table)
    .select("*")
    .eq("id", id)
    .single();

  if (error) {
    throw new Error(handleSupabaseError(error));
  }

  return data;
};

export const getRecords = async (
  table: string,
  options?: {
    select?: string;
    filter?: Record<string, any>;
    orderBy?: { column: string; ascending?: boolean };
    limit?: number;
    offset?: number;
  },
) => {
  let query = supabase.from(table).select(options?.select || "*");

  // 
  if (options?.filter) {
    Object.entries(options.filter).forEach(([key, value]) => {
      query = query.eq(key, value);
    });
  }

  // 
  if (options?.orderBy) {
    query = query.order(options.orderBy.column, {
      ascending: options.orderBy.ascending ?? true,
    });
  }

  // 
  if (options?.limit) {
    query = query.limit(options.limit);
  }

  if (options?.offset) {
    query = query.range(
      options.offset,
      options.offset + (options.limit || 10) - 1,
    );
  }

  const { data, error } = await query;

  if (error) {
    throw new Error(handleSupabaseError(error));
  }

  return data;
};
