// 
interface AxiosError {
  response?: {
    status: number;
    data: any;
  };
  request?: any;
  message: string;
}

interface NetworkError {
  request: any;
}

function isAxiosError(error: unknown): error is AxiosError {
  return typeof error === 'object' && error !== null && 
    ('response' in error || 'request' in error);
}

function isNetworkError(error: unknown): error is NetworkError {
  return typeof error === 'object' && error !== null && 'request' in error;
}

export interface AppError {
  code: string;
  message: string;
  details?: unknown;
  timestamp: Date;
}

export class ErrorHandler {
  // 
  static createError(code: string, message: string, details?: unknown): AppError {
    return {
      code,
      message,
      details,
      timestamp: new Date(),
    };
  }

  //  API 
  static handleApiError(error: unknown): AppError {
    // Axios
    if (isAxiosError(error)) {
      const status = error.response?.status;
      const data = error.response?.data;

      if (!status) {
        return this.createError(
          "UNKNOWN_ERROR", 
          "API",
          error
        );
      }
      switch (status) {
        case 400:
          return this.createError(
            "BAD_REQUEST",
            data.message || "",
            data,
          );
        case 401:
          return this.createError(
            "UNAUTHORIZED",
            "",
            data,
          );
        case 403:
          return this.createError("FORBIDDEN", "", data);
        case 404:
          return this.createError("NOT_FOUND", "", data);
        case 422:
          return this.createError("VALIDATION_ERROR", "", data);
        case 429:
          return this.createError(
            "RATE_LIMIT",
            "",
            data,
          );
        case 500:
          return this.createError("SERVER_ERROR", "", data);
        case 502:
          return this.createError("BAD_GATEWAY", "", data);
        case 503:
          return this.createError(
            "SERVICE_UNAVAILABLE",
            "",
            data,
          );
        default:
          return this.createError("HTTP_ERROR", `HTTP: ${status}`, data);
      }
    } else if (isNetworkError(error)) {
      // 
      return this.createError(
        "NETWORK_ERROR",
        "",
        error.request,
      );
    } else if (error instanceof Error) {
      // 
      return this.createError(
        "UNKNOWN_ERROR",
        error.message,
        error
      );
    } else {
      // 
      return this.createError(
        "UNKNOWN_ERROR",
        "",
        String(error)
      );
    }
  }

  // 
  static handleDatabaseError(error: any): AppError {
    if (error.code) {
      switch (error.code) {
        case "PGRST116":
          return this.createError("NOT_FOUND", "", error);
        case "PGRST301":
          return this.createError("PERMISSION_DENIED", "", error);
        case "23505":
          return this.createError("DUPLICATE_KEY", "", error);
        case "23503":
          return this.createError(
            "FOREIGN_KEY_VIOLATION",
            "",
            error,
          );
        case "23514":
          return this.createError("CHECK_VIOLATION", "", error);
        default:
          return this.createError(
            "DATABASE_ERROR",
            error.message || "",
            error,
          );
      }
    }

    return this.createError(
      "DATABASE_ERROR",
      error.message || "",
      error,
    );
  }

  // 
  static handleValidationError(errors: Record<string, string[]>): AppError {
    const messages = Object.entries(errors)
      .map(([field, fieldErrors]) => `${field}: ${fieldErrors.join(", ")}`)
      .join("; ");

    return this.createError(
      "VALIDATION_ERROR",
      `: ${messages}`,
      errors,
    );
  }

  // 
  static formatErrorMessage(error: AppError): string {
    switch (error.code) {
      case "NETWORK_ERROR":
        return "";
      case "UNAUTHORIZED":
        return "";
      case "FORBIDDEN":
        return "";
      case "NOT_FOUND":
        return "";
      case "VALIDATION_ERROR":
        return error.message;
      case "RATE_LIMIT":
        return "";
      case "SERVER_ERROR":
        return "";
      default:
        return error.message || "";
    }
  }

  // 
  static logError(error: AppError, context?: string): void {
    const logData = {
      ...error,
      context,
      userAgent: navigator.userAgent,
      url: window.location.href,
    };

    console.error("Application Error:", logData);

    // TODO: 
    // this.sendToMonitoring(logData)
  }

  // 
  // eslint-disable-next-line @typescript-eslint/no-unused-vars
  private static async sendToMonitoring(_errorData: any): Promise<void> {
    try {
      // TODO: 
      // await fetch('/api/errors', {
      //   method: 'POST',
      //   headers: { 'Content-Type': 'application/json' },
      //   body: JSON.stringify(errorData)
      // })
    } catch (err) {
      console.error("Failed to send error to monitoring service:", err);
    }
  }
}

// 
export function createErrorBoundary() {
  return {
    errorCaptured(error: Error, instance: any, info: string) {
      const appError = ErrorHandler.createError("VUE_ERROR", error.message, {
        error,
        instance,
        info,
      });

      ErrorHandler.logError(appError, "Vue Error Boundary");

      //  false 
      return false;
    },
  };
}

// 
export function setupGlobalErrorHandler() {
  //  Promise 
  window.addEventListener("unhandledrejection", (event) => {
    const error = ErrorHandler.createError(
      "UNHANDLED_PROMISE",
      event.reason?.message || "Unhandled Promise Rejection",
      event.reason,
    );

    ErrorHandler.logError(error, "Unhandled Promise Rejection");

    // 
    event.preventDefault();
  });

  //  JavaScript 
  window.addEventListener("error", (event) => {
    const error = ErrorHandler.createError("UNHANDLED_ERROR", event.message, {
      filename: event.filename,
      lineno: event.lineno,
      colno: event.colno,
      error: event.error,
    });

    ErrorHandler.logError(error, "Unhandled JavaScript Error");
  });
}

// 
export class RetryHandler {
  static async withRetry<T>(
    operation: () => Promise<T>,
    maxRetries: number = 3,
    delay: number = 1000,
  ): Promise<T> {
    let lastError: any;

    for (let attempt = 1; attempt <= maxRetries; attempt++) {
      try {
        return await operation();
      } catch (error) {
        lastError = error;

        if (attempt === maxRetries) {
          throw error;
        }

        // 
        const retryDelay = delay * Math.pow(2, attempt - 1);
        await new Promise((resolve) => setTimeout(resolve, retryDelay));
      }
    }

    throw lastError;
  }

  static async withExponentialBackoff<T>(
    operation: () => Promise<T>,
    maxRetries: number = 3,
    baseDelay: number = 1000,
    maxDelay: number = 10000,
  ): Promise<T> {
    let lastError: any;

    for (let attempt = 1; attempt <= maxRetries; attempt++) {
      try {
        return await operation();
      } catch (error) {
        lastError = error;

        if (attempt === maxRetries) {
          throw error;
        }

        // 
        const exponentialDelay = baseDelay * Math.pow(2, attempt - 1);
        const jitter = Math.random() * 0.1 * exponentialDelay;
        const delay = Math.min(exponentialDelay + jitter, maxDelay);

        await new Promise((resolve) => setTimeout(resolve, delay));
      }
    }

    throw lastError;
  }
}
