import { ref, computed } from "vue";
import { defineStore } from "pinia";
import { supabase } from "@/lib/supabaseClient";
import type { Database } from "@/types/database";
type Tables = Database["public"]["Tables"];

//  Tool  category  tags 
//  tool.category.name  tool.tags 
export type Tool = Tables["tools"]["Row"] & {
  categories: Tables["categories"]["Row"] | null;
  tool_tags: Array<{
    tags: Tables["tags"]["Row"];
  }> | null;
  tags?: string[]; //  tool_tags 
};

export const useToolsStore = defineStore("tools", () => {
  // --- State () ---
  const tools = ref<Tool[]>([]);
  const loading = ref(false);
  const error = ref<Error | null>(null);
  const initialized = ref(false);

  //  AppHeader.vue 
  const searchQuery = ref("");
  // 
  const selectedCategory = ref("all");
  //  AppHeader.vue 
  const sidebarCollapsed = ref(false);

  // --- Getters () ---

  /**
   * 
   *  searchQuery  tools 
   */
  const filteredTools = computed(() => {
    if (!searchQuery.value) {
      return tools.value;
    }
    const lowerCaseQuery = searchQuery.value.toLowerCase();
    return tools.value.filter(
      (tool) =>
        tool.name.toLowerCase().includes(lowerCaseQuery) ||
        tool.description.toLowerCase().includes(lowerCaseQuery) ||
        (tool.categories &&
          tool.categories.name.toLowerCase().includes(lowerCaseQuery)),
    );
  });

  // --- Actions () ---

  /**
   *  Supabase 
   * 
   */
  async function fetchTools() {
    if (loading.value) return;

    loading.value = true;
    error.value = null;
    try {
      // 
      const supabaseUrl = import.meta.env.VITE_SUPABASE_URL;
      const supabaseAnonKey = import.meta.env.VITE_SUPABASE_ANON_KEY;
      
      if (!supabaseUrl || !supabaseAnonKey || supabaseUrl.includes('your-project-ref') || supabaseAnonKey.includes('your-anon-key')) {
        // 
        console.warn('Supabase ');
        tools.value = [
          {
            id: '1',
            name: 'Visual Studio Code',
            description: '',
            url: 'https://code.visualstudio.com',
            icon: null,
            category_id: '1',
            is_featured: true,
            click_count: 150,
            status: 'active' as const,
            created_at: new Date().toISOString(),
            updated_at: new Date().toISOString(),
            created_by: null,
            meta_title: null,
            meta_description: null,
            sort_order: 1,
            categories: {
              id: '1',
              name: '',
              description: '',
              icon: '',
              color: '#3b82f6',
              parent_id: null,
              sort_order: 1,
              is_active: true,
              created_at: new Date().toISOString(),
              updated_at: new Date().toISOString()
            },
            tool_tags: null,
            tags: ['', '', '']
          },
          {
            id: '2', 
            name: 'Figma',
            description: '',
            url: 'https://figma.com',
            icon: null,
            category_id: '2',
            is_featured: true,
            click_count: 120,
            status: 'active' as const,
            created_at: new Date().toISOString(),
            updated_at: new Date().toISOString(),
            created_by: null,
            meta_title: null,
            meta_description: null,
            sort_order: 2,
            categories: {
              id: '2',
              name: '',
              description: 'UI/UX',
              icon: '',
              color: '#ef4444',
              parent_id: null,
              sort_order: 2,
              is_active: true,
              created_at: new Date().toISOString(),
              updated_at: new Date().toISOString()
            },
            tool_tags: null,
            tags: ['', 'UI', 'UX']
          },
          {
            id: '3',
            name: 'ChatGPT',
            description: 'AI',
            url: 'https://chat.openai.com',
            icon: null,
            category_id: '3',
            is_featured: true,
            click_count: 200,
            status: 'active' as const,
            created_at: new Date().toISOString(),
            updated_at: new Date().toISOString(),
            created_by: null,
            meta_title: null,
            meta_description: null,
            sort_order: 3,
            categories: {
              id: '3',
              name: 'AI',
              description: '',
              icon: '',
              color: '#10b981',
              parent_id: null,
              sort_order: 3,
              is_active: true,
              created_at: new Date().toISOString(),
              updated_at: new Date().toISOString()
            },
            tool_tags: null,
            tags: ['AI', '', '']
          }
        ];
        initialized.value = true;
        return;
      }

      const { data, error: queryError } = await supabase
        .from("tools")
        .select(
          `
          *,
          categories ( * ),
          tool_tags ( tags ( * ) )
        `,
        )
        .eq("status", "active")
        .order("sort_order", { ascending: true });

      if (queryError) {
        throw queryError;
      }

      // Supabase 
      //  tool_tags  tags 
      const processedTools =
        (data as unknown as Tool[])?.map((tool) => ({
          ...tool,
          tags: tool.tool_tags?.map((tt) => tt.tags.name) || [],
        })) || [];

      tools.value = processedTools;
      initialized.value = true;
    } catch (e: any) {
      console.error(":", e);
      error.value = e;
      
      // Supabase
      if (tools.value.length === 0) {
        console.warn('Supabase');
        tools.value = [
          {
            id: '1',
            name: 'Visual Studio Code',
            description: '',
            url: 'https://code.visualstudio.com',
            icon: null,
            category_id: '1',
            is_featured: true,
            click_count: 150,
            status: 'active' as const,
            created_at: new Date().toISOString(),
            updated_at: new Date().toISOString(),
            created_by: null,
            meta_title: null,
            meta_description: null,
            sort_order: 1,
            categories: {
              id: '1',
              name: '',
              description: '',
              icon: '',
              color: '#3b82f6',
              parent_id: null,
              sort_order: 1,
              is_active: true,
              created_at: new Date().toISOString(),
              updated_at: new Date().toISOString()
            },
            tool_tags: null,
            tags: ['', '', '']
          }
        ];
        initialized.value = true;
        error.value = null; // 
      }
    } finally {
      loading.value = false;
    }
  }

  /**
   *  Store
   *  ErrorDisplay.vue 
   */
  async function initialize() {
    if (!initialized.value) {
      await fetchTools();
    }
  }

  /**
   * 
   *  AppHeader.vue 
   */
  function toggleSidebar() {
    sidebarCollapsed.value = !sidebarCollapsed.value;
  }

  /**
   * 
   */
  function clearError() {
    error.value = null;
  }

  /**
   * 
   */
  function setSearchQuery(query: string) {
    searchQuery.value = query;
  }

  /**
   * 
   */
  function setSelectedCategory(categoryId: string) {
    selectedCategory.value = categoryId;
  }

  /**
   * 
   */
  async function incrementClickCount(toolId: string) {
    try {
      const { error } = await supabase.rpc("increment_click_count", {
        tool_id: toolId,
      });

      if (error) {
        console.error(":", error);
        return;
      }

      // 
      const tool = tools.value.find((t) => t.id === toolId);
      if (tool) {
        tool.click_count = (tool.click_count || 0) + 1;
      }
    } catch (error) {
      console.error(":", error);
    }
  }

  /**
   * 
   */
  async function toggleFavorite(toolId: string) {
    try {
      // TODO: 
      console.log(":", toolId);
    } catch (error) {
      console.error(":", error);
    }
  }

  // --- Return () ---
  // 
  return {
    // State
    tools,
    loading,
    error,
    initialized,
    searchQuery,
    selectedCategory,
    sidebarCollapsed,
    // Getters
    filteredTools,
    // Actions
    fetchTools,
    initialize,
    toggleSidebar,
    clearError,
    setSearchQuery,
    setSelectedCategory,
    incrementClickCount,
    toggleFavorite,
  };
});
