import { ref, computed } from "vue";
import { defineStore } from "pinia";
import { supabase } from "@/lib/supabaseClient";
import type { User as SupabaseUser, AuthError } from "@supabase/supabase-js";
import type { Database } from "@/types/database";

/**
 * 
 *  Supabase Auth User  user_profiles 
 */
export type UserProfile = SupabaseUser & {
  username: string;
  avatar_url: string;
  role: "user" | "admin" | "super_admin";
};

export const useAuthStore = defineStore("auth", () => {
  // --- State () ---
  const user = ref<UserProfile | null>(null);
  const loading = ref(false);
  const initialized = ref(false); // 
  const error = ref<AuthError | null>(null);

  // --- Getters () ---

  /**
   * 
   */
  const isAuthenticated = computed(() => !!user.value);

  /**
   * 
   *  AppHeader.vue 
   */
  const isAdmin = computed(() => {
    return user.value?.role === "admin" || user.value?.role === "super_admin";
  });

  // --- Actions () ---

  /**
   *  user_profiles 
   * @param userId - Supabase  ID
   */
  async function fetchUserProfile(
    userId: string,
  ): Promise<Database["public"]["Tables"]["user_profiles"]["Row"] | null> {
    const { data, error: profileError } = await supabase
      .from("user_profiles")
      .select("*")
      .eq("id", userId)
      .single();

    if (profileError) {
      console.error(
        ` (ID: ${userId}):`,
        profileError.message,
      );
      // 
      error.value = {
        name: "ProfileFetchError",
        message: `: ${profileError.message}`,
      } as AuthError;
      return null;
    }
    return data;
  }

  /**
   *  Store
   *  Store  Supabase 
   * 
   */
  function initialize() {
    if (initialized.value) return;

    loading.value = true;

    supabase.auth.onAuthStateChange(async (_event, session) => {
      if (session?.user) {
        const profile = await fetchUserProfile(session.user.id);
        if (profile) {
          //  Supabase Auth User  user_profiles 
          user.value = {
            ...session.user,
            username: profile.username || "",
            avatar_url: profile.avatar_url || "",
            role: profile.role || "user",
          };
        } else {
          //  profile user 
          user.value = {
            ...session.user,
            username: session.user.email || "",
            avatar_url: "",
            role: "user",
          };
        }
      } else {
        user.value = null;
      }
      // 
      initialized.value = true;
      loading.value = false;
    });
  }

  /**
   * 
   *  AppHeader.vue 
   */
  async function logout() {
    loading.value = true;
    error.value = null;
    try {
      const { error: signOutError } = await supabase.auth.signOut();
      if (signOutError) throw signOutError;
      // onAuthStateChange  user.value  null
    } catch (e: any) {
      error.value = e;
      console.error(":", e);
    } finally {
      loading.value = false;
    }
  }

  /**
   * 
   *  ErrorDisplay.vue 
   */
  function clearError() {
    error.value = null;
  }

  // --- Return () ---
  return {
    // State
    user,
    loading,
    initialized,
    error,
    // Getters
    isAuthenticated,
    isAdmin,
    // Actions
    initialize,
    logout,
    clearError,
    //  login, register, etc.
  };
});
