import {
  createRouter,
  createWebHistory,
  type RouteRecordRaw,
} from "vue-router";

// 
const routes: RouteRecordRaw[] = [
  {
    path: "/",
    name: "Home",
    component: () => import("../views/HomeView.vue"),
    meta: {
      title: "",
      description: "",
    },
  },

  {
    path: "/search",
    name: "SearchResults",
    component: () => import("../views/SearchResultsView.vue"),
    meta: {
      title: "",
      description: "",
    },
  },

  // 
  {
    path: "/help",
    name: "HelpSupport",
    component: () => import("../views/HelpSupportView.vue"),
    meta: {
      title: "",
      description: "",
    },
  },
  {
    path: "/faq",
    name: "FAQ",
    component: () => import("../views/FAQView.vue"),
    meta: {
      title: "",
      description: "",
    },
  },
  {
    path: "/user-guide",
    name: "UserGuide",
    component: () => import("../views/UserGuideView.vue"),
    meta: {
      title: "",
      description: "",
    },
  },
  {
    path: "/guide",
    redirect: "/user-guide",
  },
  {
    path: "/feedback",
    name: "Feedback",
    component: () => import("../views/FeedbackView.vue"),
    meta: {
      title: "",
      description: "",
    },
  },
  {
    path: "/contact",
    name: "Contact",
    component: () => import("../views/ContactView.vue"),
    meta: {
      title: "",
      description: "",
    },
  },
  {
    path: "/terms",
    name: "TermsOfService",
    component: () => import("../views/TermsOfServiceView.vue"),
    meta: {
      title: "",
      description: "",
    },
  },
  {
    path: "/upload-product",
    name: "ProductUpload",
    component: () => import("../views/ProductUploadView.vue"),
    meta: {
      title: "",
      description: "",
      requiresAuth: true,
    },
  },
  {
    path: "/tools",
    name: "Tools",
    component: () => import("../views/ToolsView.vue"),
    meta: {
      title: "",
      description: "",
    },
  },
  {
    path: "/tools/:id",
    name: "ToolDetail",
    component: () => import("../views/ToolDetailView.vue"),
    meta: {
      title: "",
      description: "",
    },
  },
  {
    path: "/products",
    name: "Products",
    component: () => import("@/views/ProductsView.vue"),
    meta: {
      title: "",
      description: "",
    },
  },
  {
    path: "/product/:id",
    name: "ProductDetail",
    component: () => import("../views/ProductDetailView.vue"),
    meta: {
      title: "",
      description: "",
    },
  },
  {
    path: "/user",
    name: "User",
    component: () => import("../views/UserView.vue"),
    meta: {
      title: "",
      description: "",
      requiresAuth: true,
    },
    children: [
      {
        path: "profile",
        name: "UserProfile",
        component: () => import("../views/user/ProfileView.vue"),
        meta: {
          title: "",
          requiresAuth: true,
        },
      },
      {
        path: "favorites",
        name: "UserFavorites",
        component: () => import("../views/user/FavoritesView.vue"),
        meta: {
          title: "",
          requiresAuth: true,
        },
      },
      {
        path: "orders",
        name: "UserOrders",
        component: () => import("../views/user/OrdersView.vue"),
        meta: {
          title: "",
          requiresAuth: true,
        },
      },
    ],
  },
  {
    path: "/auth",
    name: "Auth",
    component: () => import("../views/AuthView.vue"),
    meta: {
      title: "",
      description: "",
    },
    children: [
      {
        path: "login",
        name: "Login",
        component: () => import("../views/auth/LoginView.vue"),
        meta: {
          title: "",
        },
      },
      {
        path: "register",
        name: "Register",
        component: () => import("../views/auth/RegisterView.vue"),
        meta: {
          title: "",
        },
      },
      {
        path: "forgot-password",
        name: "ForgotPassword",
        component: () => import("../views/auth/ForgotPasswordView.vue"),
        meta: {
          title: "",
        },
      },
    ],
  },
  {
    path: "/admin",
    name: "Admin",
    component: () => import("../views/AdminView.vue"),
    meta: {
      title: "",
      description: "",
      requiresAuth: true,
      requiresAdmin: true,
    },
    children: [
      {
        path: "",
        redirect: "/admin/dashboard",
      },
      {
        path: "dashboard",
        name: "AdminDashboard",
        component: () => import("../views/admin/DashboardView.vue"),
        meta: {
          title: "",
          requiresAuth: true,
          requiresAdmin: true,
        },
      },
      {
        path: "tools",
        name: "AdminTools",
        component: () => import("../views/AdminToolsView.vue"),
        meta: {
          title: "",
          requiresAuth: true,
          requiresAdmin: true,
        },
      },
      {
        path: "products",
        name: "AdminProducts",
        component: () => import("../views/admin/ProductManagementView.vue"),
        meta: {
          title: "",
          requiresAuth: true,
          requiresAdmin: true,
        },
      },
      {
        path: "users",
        name: "AdminUsers",
        component: () => import("../views/admin/UserManagementView.vue"),
        meta: {
          title: "",
          requiresAuth: true,
          requiresAdmin: true,
        },
      },
      {
        path: "orders",
        name: "AdminOrders",
        component: () => import("../views/admin/OrderManagementView.vue"),
        meta: {
          title: "",
          requiresAuth: true,
          requiresAdmin: true,
        },
      },
      {
        path: "local",
        name: "AdminLocal",
        component: () => import("../views/admin/LocalManagementView.vue"),
        meta: {
          title: "",
          requiresAuth: true,
          requiresAdmin: true,
        },
      },
      {
        path: "settings",
        name: "AdminSettings",
        component: () => import("../views/admin/SettingsView.vue"),
        meta: {
          title: "",
          requiresAuth: true,
          requiresAdmin: true,
        },
      },
    ],
  },
  {
    path: "/payment",
    name: "Payment",
    component: () => import("../views/PaymentView.vue"),
    meta: {
      title: "",
      description: "",
      requiresAuth: true,
    },
  },
  {
    path: "/payment/success",
    name: "PaymentSuccess",
    component: () => import("../views/PaymentSuccessView.vue"),
    meta: {
      title: "",
      description: "",
    },
  },
  {
    path: "/payment/cancel",
    name: "PaymentCancel",
    component: () => import("../views/PaymentCancelView.vue"),
    meta: {
      title: "",
      description: "",
    },
  },
  {
    path: "/settings",
    name: "SimpleSettings",
    component: () => import("../views/SimpleSettingsView.vue"),
    meta: {
      title: "",
      description: "",
    },
  },
  {
    path: "/test-settings",
    name: "TestSettings",
    component: () => import("../views/TestSettingsView.vue"),
    meta: {
      title: "",
      description: "",
    },
  },

  {
    path: "/:pathMatch(.*)*",
    name: "NotFound",
    component: () => import("../views/NotFoundView.vue"),
    meta: {
      title: "",
      description: "",
    },
  },
  //  - 
];

// 
const router = createRouter({
  history: createWebHistory(),
  routes,
});

export { router, routes };
